.slide-in {
  opacity: 0;
  transition: all .5s;
}

.align-left {
   float: left;
   margin-right: 20px;
 }

 .align-right {
   float: right;
   margin-left: 20px;
 }

.align-left.slide-in {
  transform: translateX(-30%) scale(0.95);
}

.align-right.slide-in {
  transform: translateX(30%) scale(0.95);
}

.slide-in.active {
  opacity: 1;
  transform: translateX(0%) scale(1);
}
